.App {
  display: flex;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.portfolio-image {
  height: 100vh;
  width: 75%;
  object-fit: contain;
  background: no-repeat 20% 20%;
  background-size: cover;
  -webkit-box-reflect: right 0px -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(0, transparent), to(rgba(255, 255, 255, 0.3)));
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
